* {
  color: black;
  font-size: large;
  font-family: sans-serif;
}
.App {
  text-align: center;
  margin: 0 auto; 
  background-color: rgba(138, 138, 138, 0.384);
  width: 100vw;
  height: 100vh;
}
body {
  background-image:url(https://p0.piqsels.com/preview/108/237/98/bokeh-photography-of-lights.jpg);
  background-repeat: no-repeat;  
  background-size: 100vw 100vh;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img {
  max-height: 300px;
}

.carousel-indicators{
  height: 25px;
}

.Carousel-container {
  display: flex;
  justify-content: center;
  

}

.Carousel {
  width: 390px;
  border: 4px solid black;
  padding-left: 2px;

}

.message {
  background-color: green;
}
